.b-head-search {
  display: block;
  position: absolute;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  height: 0; }
  .b-head-search__icon {
    display: inline-block;
    position: absolute;
    top: 0;
    padding: 18px 20px 17px;
    right: 0; }
  .b-head-search__icon:hover {
    background: #4c0877; }
  .b-head-search__icon .icon-svg {
    font-size: 25px;
    color: #fff; }
  .b-head-search__form {
    position: absolute;
    left: 0;
    width: 100%;
    background: #0b1226;
    height: 60px;
    padding: 8px 15px;
    opacity: 0;
    transition: opacity .3s;
    display: flex; }
  .b-head-search__form .form-group {
    display: inline-block;
    margin-bottom: 0;
    vertical-align: middle;
    flex: 1 0 auto; }
  .b-head-search__form .btn {
    flex: 0 1 auto;
    height: 45px;
    margin-left: 3px;
    line-height: 0;
    display: inline-flex;
    align-self: center; }
  .b-head-search__close {
    display: inline-block;
    float: right;
    vertical-align: middle;
    padding: 6px;
    cursor: pointer;
    transition: .15s;
    flex: 0 1 auto;
    margin-left: 15px; }
  .b-head-search__close:hover {
    background: black; }
  .b-head-search__close .icon-svg {
    font-size: 32px;
    color: #fff; }
  .b-head-search.is-open .b-head-search__form {
    opacity: 1; }
